import {
	css,
	customElement,
	html,
	nothing,
	property,
	when,
} from '@umbraco-cms/backoffice/external/lit';
import { UmbLitElement } from '@umbraco-cms/backoffice/lit-element';
import { SnapshotInfo } from '../../api';
import { TimeOptions } from '../../constants';

/**
 * @fires snapshot-apply - When the apply button is clicked
 * @fires snapshot-report - When the report button is clicked
 * @fires snapshot-download - When the download button is clicked
 * @fires snapshot-remove - When the remove button is clicked
 */
@customElement('usync-snapshot-table')
export default class uSyncSnapshotsTableElement extends UmbLitElement {
	@property({ type: Array })
	snapshots?: SnapshotInfo[];

	#onSnapshotAction(snapshot: SnapshotInfo, action: string) {
		this.dispatchEvent(
			new CustomEvent(`snapshot-${action}`, {
				bubbles: true,
				composed: true,
				detail: snapshot,
			}),
		);
	}

	render() {
		return this.snapshots?.length === 0
			? nothing
			: html` <uui-table
					>${this.renderTableHeader()} ${this.renderTableBody()}</uui-table
				>`;
	}

	renderTableHeader() {
		return html`
			<uui-table-head>
				<uui-table-head-cell>Name</uui-table-head-cell>
				<uui-table-head-cell>Changes</uui-table-head-cell>
				<uui-table-head-cell>Created</uui-table-head-cell>
				<uui-table-head-cell>Applied</uui-table-head-cell>
				<uui-table-head-cell>Local</uui-table-head-cell>
				<uui-table-head-cell>Actions</uui-table-head-cell>
			</uui-table-head>
		`;
	}

	renderTableBody() {
		return this.snapshots?.map((snapshot) => {
			return html`<uui-table-row>
				<uui-table-cell>${snapshot.name}</uui-table-cell>
				<uui-table-cell>${snapshot.fileCount}</uui-table-cell>
				<uui-table-cell>${this.renderTime(snapshot.created)}</uui-table-cell>
				<uui-table-cell>${this.renderTime(snapshot.applied)}</uui-table-cell>
				<uui-table-cell
					>${when(
						snapshot.local == true,
						() => html`<umb-icon name="icon-check color-green"></umb-icon>`,
					)}</uui-table-cell
				>
				<uui-table-cell>
					<uui-button
						compact
						label="Apply"
						color="positive"
						size="small"
						title="Apply the snapshot"
						@click=${() => this.#onSnapshotAction(snapshot, 'apply')}>
						<uui-icon name="icon-add"></uui-icon>
					</uui-button>
					<uui-button
						compact
						label="Report"
						color="default"
						size="small"
						title="report"
						@click=${() => this.#onSnapshotAction(snapshot, 'report')}>
						<uui-icon name="icon-info"></uui-icon>
					</uui-button>
					<uui-button
						compact
						label="Download"
						color="positive"
						size="small"
						title="download"
						@click=${() => this.#onSnapshotAction(snapshot, 'download')}>
						<uui-icon name="icon-download-alt"></uui-icon>
					</uui-button>
					<uui-button
						compact
						label="Remove"
						color="danger"
						size="small"
						title="remove"
						@click=${() => this.#onSnapshotAction(snapshot, 'remove')}>
						<uui-icon name="icon-trash"></uui-icon
					></uui-button>
				</uui-table-cell>
			</uui-table-row>`;
		});
	}

	renderTime(time: string) {
		return time == '0001-01-01T00:00:00'
			? html`Never`
			: html`${this.localize.date(time, TimeOptions)}`;
	}

	static styles = css``;
}
