import { uSyncSnapshots } from '../constants';

const workspaceAlias = uSyncSnapshots.workspace;

const workspace: UmbExtensionManifest = {
	type: 'workspace',
	alias: workspaceAlias,
	name: 'Snapshots',
	js: () => import('./snapshots.element'),
	meta: {
		entityType: uSyncSnapshots.element,
	},
};

const views: UmbExtensionManifest[] = [
	{
		type: 'workspaceView',
		alias: 'usync.snapshots.view.default',
		name: 'Snapshots default view',
		js: () => import('./views/default.element'),
		weight: 200,
		meta: {
			label: 'Snapshots',
			pathname: 'default',
			icon: uSyncSnapshots.icon,
		},
		conditions: [
			{
				alias: 'Umb.Condition.WorkspaceAlias',
				match: workspaceAlias,
			},
		],
	},
];

export const manifests = [workspace, ...views];
